function addMessage(message, loadingState, historyItem) {
  message.history = message.history || {};
  message.ui_state_internal = message.ui_state_internal || {};
  message.ui_state_internal.from_history = true;
  message.history.timestamp = new Date(historyItem.time).getTime();
  if (message.thread_id !== THREAD_ID_MAIN) {
    loadingState.lastThreadID = message.thread_id;
  }
  loadingState.allMessagesByID[message.id] = message;
  loadingState.allMessages.push(message);
}

function createLocalMessages(loadingState) {
  const {allMessages: allMessages, allLocalMessagesByID: allLocalMessagesByID, localMessagesByOriginalMessageID: localMessagesByOriginalMessageID} = loadingState;
  allMessages.forEach(message => {
    if (isRequest(message)) {
      if (!message.history?.silent) {
        const text = message.history?.label || message.input.text;
        const localMessage = inputItemToLocalItem(message, text);
        localMessagesByOriginalMessageID[message.id].push(localMessage);
        allLocalMessagesByID[localMessage.ui_state.id] = localMessage;
      }
    } else {
      const items = getResponseItems(message);
      if (items?.length) {
        items.forEach(messageItem => {
          if (!isPause(messageItem)) {
            const localMessage = outputItemToLocalItem(messageItem, message, false);
            localMessagesByOriginalMessageID[message.id].push(localMessage);
            allLocalMessagesByID[localMessage.ui_state.id] = localMessage;
            if (isResponseWithNestedItems(localMessage.item)) {
              const nestedLocalMessageItems = [];
              createLocalMessageItemsForNestedMessageItems(localMessage, message, true, nestedLocalMessageItems, true);
              nestedLocalMessageItems.forEach(localMessageItem => {
                const localMessageID = localMessageItem.ui_state.id;
                loadingState.loadedHistory.messageHistory.allMessageItemsByID[localMessageID] = localMessageItem;
              });
            }
          }
        });
      }
    }
    addMessageToThread(message, loadingState);
  });
}

function getResponseItems(message) {
  if (isResponse(message)) {
    return message.output.generic;
  }
  return null;
}

function addMessageToThread(message, loadingState) {
  const {threadMessagesByThreadID: threadMessagesByThreadID} = loadingState;
  let thread = threadMessagesByThreadID[THREAD_ID_MAIN];
  if (!thread) {
    thread = [];
    threadMessagesByThreadID[THREAD_ID_MAIN] = thread;
  }
  thread.push(message);
}

function createChatStates(loadingState) {
  const {loadedHistory: loadedHistory, threadMessagesByThreadID: threadMessagesByThreadID, localMessagesByOriginalMessageID: localMessagesByOriginalMessageID} = loadingState;
  loadedHistory.messageHistory.botMessageState = toChatMessageState(threadMessagesByThreadID[THREAD_ID_MAIN], localMessagesByOriginalMessageID);
}

function toChatMessageState(messages, localMessagesByFullMessageID) {
  const localMessageIDs = [];
  const messageIDs = [];
  if (messages) {
    messages.forEach(message => {
      messageIDs.push(message.id);
      localMessagesByFullMessageID[message.id].forEach(localMessage => {
        localMessageIDs.push(localMessage.ui_state.id);
      });
    });
  }
  return {
    ...DEFAULT_CHAT_MESSAGES_STATE,
    localMessageIDs: localMessageIDs,
    messageIDs: messageIDs
  };
}

function markIsLatestWelcomeNode(loadingState) {
  const {responsesByRequestID: responsesByRequestID, threadMessagesByThreadID: threadMessagesByThreadID, localMessagesByOriginalMessageID: localMessagesByOriginalMessageID} = loadingState;
  const mainThreadMessages = threadMessagesByThreadID[THREAD_ID_MAIN];
  const welcomeRequest = findLast(mainThreadMessages, message => isRequest(message) && message.history.is_welcome_request);
  if (welcomeRequest) {
    const welcomeResponse = responsesByRequestID[welcomeRequest.id];
    if (welcomeResponse) {
      localMessagesByOriginalMessageID[welcomeResponse.id].forEach(localMessage => {
        localMessage.ui_state.isWelcomeResponse = true;
      });
    }
  }
}

function markSelectedOptions({allMessages: allMessages, relatedMessageByID: relatedMessageByID, localMessagesByOriginalMessageID: localMessagesByOriginalMessageID}) {
  allMessages.forEach(message => {
    if (isResponse(message)) {
      localMessagesByOriginalMessageID[message.id].forEach(localMessage => {
        if (isOptionItem(localMessage.item)) {
          const relatedRequest = relatedMessageByID[message.id];
          if (isRequest(relatedRequest)) {
            localMessage.ui_state.optionSelected = relatedRequest;
          }
        } else if (isDateResponseType(localMessage)) {
          const relatedRequest = relatedMessageByID[message.id];
          if (isRequest(relatedRequest)) {
            localMessage.ui_state.originalUserText = relatedRequest.history.label;
          }
        }
      });
    }
  });
}

